.r-file-upload__progress,
.r-file-upload__status {
  background-color: #F7F8F9;
}

.r-file-upload__progress button,
.r-file-upload__status button {
  border: 0;
}

.r-file-upload__progress {
  padding: 0.5rem;
  height: 62px;
}

.r-file-upload__status {
  display: flex;
  flex-direction: row;
  position: relative;
  font-size: 0.75rem;
  height: 52px;
  border-radius: 2px;
}

.r-file-upload__status__icon {
  display: flex;
  align-items: center;
  font-size: 1rem;
  color: #139C3E;
  padding-left: 0.75rem;
  padding-right: 0.5rem;
}

.r-file-upload__status__content {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.r-file-upload__status__content-label {
  font-weight: 700;
}

.r-file-upload__status__content-text {
  font-weight: 400;
  color: rgba(32, 39, 55, 0.8);
}

.r-file-upload__status button {
  position: absolute;
  top: 3px;
  background-color: transparent;
  cursor: pointer;
  right: 8px;
}

.r-file-upload__status-error {
  background-color: #FAE8E9;
}

.r-file-upload__status-error .r-file-upload__status__icon {
  color: #D01A1F;
}